.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.movieList {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-width: 1280px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around; }

@media (max-width: 600px) {
  .movieList {
    -webkit-justify-content: center;
            justify-content: center; } }

.movieLayout {
  display: -webkit-flex;
  display: flex;
  margin: 40px;
  width: 330px;
  height: 580px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer; }

.posterContainer {
  width: 330px;
  height: 330px;
  border-radius: 50%;
  border: 1px solid black;
  margin-bottom: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-size: cover; }

.movieTitle {
  font-size: 28px;
  color: #0f3b54;
  font-weight: bold; }

.newMovieTitle {
  text-align: center; }

.movieData {
  text-align: left;
  min-height: 204px;
  font-size: 16px;
  width: 100%; }

p {
  margin: 10px 0; }
  p span {
    font-weight: bold; }

.movieButtonsContainer {
  position: absolute;
  display: none; }

@media (max-width: 999px) {
  .movieButtonsContainer {
    position: unset;
    top: auto;
    display: block; } }

.formLayout {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  width: 880px;
  height: 500px; }
  .formLayout img {
    height: 500px; }
  .formLayout form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%; }
  .formLayout p {
    text-align: left;
    margin-bottom: 0; }
  .formLayout input {
    display: block;
    width: 350px;
    height: 35px;
    border: none;
    border-bottom: 1px solid black;
    font-size: 25px;
    padding: 0 10px; }
  .formLayout span {
    color: #cc0e0e;
    font-weight: 500;
    padding-left: 5px; }
  .formLayout input:focus, .formLayout textarea:focus, .formLayout select:focus {
    outline: none;
    background: #0f3b5424; }

.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: white;
  z-index: 500; }

@media (min-width: 320px) and (max-width: 1024px) {
  .modal {
    top: 60%; } }

.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 100; }

